.head{
    z-index: 3;
    position: fixed;
    top: 0;
    background-color: rgb(255, 255, 255);
    width: 100%;
    height: 10vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    /* border: 5px solid black; */
}

.btn{
    background: none;
    border: none;
    background-color: rgb(0, 0, 0);
    color: white;
    position: absolute;
    right: 20px;
    padding: 5px;
    font-size: 15px;
    cursor: pointer;
}