.chat{
    display: flex;
    flex-direction: column;
    overflow: hidden;
    /* border: 1px solid black; */
    height: 70vh;
    padding: 30px;
    position: relative;
}

.text{
    display: flex;
    flex-direction: row;
    gap: 20px;
    /* border: 1px solid black; */
    align-items: center;
    padding-left: 5vw;
    position: absolute;
    bottom: 0%;
}

.text p{
    background-color: black;
    color: white;
    padding: 5px;
    font-size: 15px;
    border-radius: 5px;
    cursor: pointer;
}

.text input{
    padding-left: 15px;
    font-size: large;
    font-weight: 200;
    border-radius: 30px;
    border: none;
    height: 30px;
    width: 50vw;
}

.text input:focus{
    outline: none;
}

.message{
    padding-left: 5vw;
    position: relative;
    height: 50vh;
    overflow: scroll;
}

.sent{
    background-color: rgba(0, 0, 255, 0.321);
    padding: 5px;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    position: relative;
    right: -80%;
    width: fit-content;
    margin-top: 3px;
}

.get{
    background-color: rgba(0, 128, 0, 0.285);
    padding: 5px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    position: relative;
    left: 1%;
    width: fit-content;
    margin-top: 3px;
}

@media screen and (max-width: 600px) {
    .chat{
        overflow: hidden;
        /* border: 1px solid black; */

        padding: 0px;
        position: relative;
    }
}