.contacts{
    display: flex;
    flex-direction: column;
    background-color: white;
    /* border: 5px solid black; */
    gap: 20px;
    padding: 30px;
    overflow: scroll;
    height: 78vh;
}

.contact{
    display: grid;
    grid-template-columns: 30% 70%;
    /* border: 1px solid black; */
    align-items: center;
    border-radius: 10px;
}

.contact:hover{
    background-color: rgb(245, 245, 245);
}

.contact p{
    font-size: 25px;
    font-weight: 600;
    /* border: 1px solid black; */
    margin-left: 10px;
}

.contact img{
    height: 60px;
    width: 60px;
    border-radius: 50%;
    padding: 3px;
    background-color: rgb(226, 226, 226);
    margin-left: 30px;
}

@media screen and (max-width: 600px) {
    .contacts{
        gap: 2px;
        padding: 5px;
       margin: 0;
        overflow: scroll;
        
    }

    .contact{
        height: 100px;
        margin: 0;
    }
    .contact p{
        font-size: 25px;
        font-weight: 600;
        /* border: 1px solid black; */
        margin: 0;
    }
    
    .contact img{
        height: 60px;
        width: 60px;
        border-radius: 50%;
        background-color: rgb(226, 226, 226);
        /* border: 1px solid black; */
        margin: 0;
        margin-left: 10px;
    }
}
  