*{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.login{
display: grid;
place-content: center;
}

.inp{
    border: 1px solid black;
    border-radius: 10px;
    margin-top: 10px;
    height: 20px;
    padding: 2px;
    padding-left: 10px;
}

.inp:focus{
    outline: none;
    border: 2px solid black;

}

.bt{
    background: none;
    border: none;
    border-radius: 10px;
    margin-top: 10px;
    font-size: 15px;
    border: 1px solid black;
}

.bt:hover{
    background-color: black;
    color: white;
}

h1{
    text-align: center;
}