.window {
  display: grid;
  grid-template-columns: 25% 75%;
  padding: 20px;
  /* border: 11px solid black; */
  height:max-content;
  height: 90vh;
  margin-top: 10vh;
}

.App{
  background-color: rgb(245, 245, 245);
}

img{
  height: 60px;
  width: 60px;
  border-radius: 50%;
  padding: 3px;
  background-color: rgb(226, 226, 226);
  margin-left: 30px;
  margin-top: 15vh;
  display: none;
}


@media screen and (max-width: 600px) {
  .window{
      grid-template-columns: 100%;
  }
  .c{
    display: none;
  }

  img{
    display: block;
  }
}

#close{
  object-fit: cover;
  display: none;
}